        <li>营销中心</li>
        <li>手机尚品汇</li>
        <li>销售联盟</li>
        <li>尚品汇社区</li>
      </ul>
      <div class="address">地址：绍兴市上虞区裴家村111</div>
      <div class="beian">京ICP备19006430号
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Register',
    data() {
      return {
        //收集表单数据
        phone:'',
        // 验证码
        code:'',
        //收集密码
        password:'',
        //确认密码
        password1:'',
        //是否同意文件
        agree:true
      }
    },
    methods:{
      async getCode(){
        //判断有没有手机号
        try{
          this.phone && await this.$store.dispatch('getSendCode',this.phone)
          // 将组件的code的属性值变为仓库中获取的
          this.code = this.$store.state.users.code
        }
        catch(error){

        }
      },
      //进行用户注册
      async userRegister(){
        try{  //成功则路由跳转到登录
           const {phone,code,password,password1} = this;
          //都为真才能发请求
          (phone&&code&&password==password1) && await this.$store.dispatch('UserRegister', {phone,code,password});
          if(phone&&code&&password==password1)
          this.$router.push('/login')
        }
        catch(error){   
          alert(error.message)
        }
      }
    }
  }
</script>

