<template>
  <div>
    <TypeNav />
    <div class="main">
      <div class="py-container">
        <!--bread-->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#">全部结果</a>
            </li>
          </ul>
          <ul class="fl sui-tag">
            <!-- 分类的面包屑 -->
            <li class="with-x" v-if="searchParamas.categoryName">
              {{ searchParamas.categoryName }} <i @click="removeCategory">x</i>
            </li>
            <!-- 关键字的面包屑 -->
            <li class="with-x" v-if="searchParamas.keyword">
              {{ searchParamas.keyword }} <i @click="removeKeyword">x</i>
            </li>
            <!-- 品牌的面包屑 -->
            <li class="with-x" v-if="searchParamas.trademark">
              {{ searchParamas.trademark.split(":")[1] }}
              <i @click="removeTrademark">x</i>
            </li>
            <!-- 平台售卖的属性值 -->
            <li
              class="with-x"
              v-for="(attrValue, index) in searchParamas.props"
              :key="index"
            >
              {{ attrValue.split(":")[2] }}
              <i @click="removeAttrInfo(index)">x</i>
            </li>
          </ul>
        </div>

        <!--selector-->
        <SearchSelector @trademarkInfo="trademarkInfo" @attrInfo="attrInfo" />

        <!--details-->
        <div class="details clearfix">
          <div class="sui-navbar">
            <div class="navbar-inner filter">
              <ul class="sui-nav">
                <li :class="{ active: isOne }" @click="changeOrder('1')">
                  <a
                    >综合
                    <span
                      v-show="isOne"
                      class="iconfont"
                      :class="{
                        'icon-jiantou_xiangshang': isAsc,
                        'icon-jiantou_xiangxia': isDesc,
                      }"
                    ></span>
                  </a>
                </li>
                <li :class="{ active: isTwo }" @click="changeOrder('2')">
                  <a
                    >价格
                    <span
                      v-show="isTwo"
                      class="iconfont"
                      :class="{
                        'icon-jiantou_xiangshang': isAsc,
                        'icon-jiantou_xiangxia': isDesc,
                      }"
                    ></span
                  ></a>
                </li>
              </ul>
            </div>
          </div>
          <!-- 此处跳转到详情页 -->
          <div class="goods-list">
            <ul class="yui3-g">
              <li class="yui3-u-1-5" v-for="good in goodsList" :key="good.id">
                <div class="list-wrap">
                  <div class="p-img">
                    <router-link
                      :to="`detail/${good.id}`"
                      ><img v-lazy="good.defaultImg"
                    /></router-link>
                  </div>
                  <div class="price">
                    <strong>
                      <em>¥</em>
                      <i>{{ good.price }}.00</i>
                    </strong>
                  </div>
                  <div class="attr">
                    <a target="_blank" href="item.html">{{ good.title }}</a>
                  </div>
                  <div class="commit">
                    <i class="command">已有<span>2000</span>人评价</i>
                  </div>
                  <div class="operate">
                    <a
                      href="success-cart.html"
                      target="_blank"
                      class="sui-btn btn-bordered btn-danger"
                      >加入购物车</a
                    >
                    <a href="javascript:void(0);" class="sui-btn btn-bordered"
                      >收藏</a
                    >
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <!-- 分页器 : 测试版，先使用假数据，随后向服务器请求替换 -->
          <Pagination
            :pageNo="searchParamas.pageNo"
            :pageSize="searchParamas.pageSize"
            :total="total"
            :continues="5"
            @getPageNo="getPageNo"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchSelector from "./SearchSelector/SearchSelector";
import { mapGetters } from "vuex";
import { mapState } from "vuex";
export default {
  name: "Search",
  data() {
    return {
      //带给服务器的参数
      searchParamas: {
        category1Id: "",
        category2Id: "",
        category3Id: "",
        //分类名字
        categoryName: "",
        //用户搜索关键字
        keyword: "",
        // 排序
        order: "1:desc",
        // 分页器参数
        pageNo: 1, //代表当前页码，默认为1
        // 每一页展示数据个数
        pageSize: 3,
        // 平台售卖属性操作带的参数
        props: [],
        // 品牌
        trademark: "",
      },
    };
  },
  components: {
    SearchSelector,
  },
  // 当组件挂载完毕后执行一次
  beforeMount() {
    //通过Object.assign合并参数
    Object.assign(this.searchParamas, this.$route.query, this.$route.params);
  },
  mounted() {
    this.getData();
  },
  methods: {
    //把这次请求封装为一个函数，当需要调用时调用即可
    getData() {
      this.$store.dispatch("getSearchList", this.searchParamas);
    },
    removeCategory() {
      this.searchParamas.categoryName = "";
      //带给服务器参数都是可有可无的，如果属性值为空，还是会把相应的字段带给服务器，但是如果把相应的字段变为undifined，则不会带给服务器
      this.searchParamas.category1Id = undefined;
      this.searchParamas.category2Id = undefined;
      this.searchParamas.category3Id = undefined;
      this.getData();
      if (this.$route.params)
        //删除后，返回到search路由,但如果路径当中出现params参数，需要携带
        this.$router.push({ name: "search", params: this.$route.params });
    },
    removeKeyword() {
      this.searchParamas.keyword = undefined;
      //带给服务器参数都是可有可无的，如果属性值为空，还是会把相应的字段带给服务器，但是如果把相应的字段变为undifined，则不会带给服务器
      this.searchParamas.category1Id = undefined;
      this.searchParamas.category2Id = undefined;
      this.searchParamas.category3Id = undefined;
      this.getData();
      if (this.$route.query)
        //删除后，返回到search路由,但如果路径当中出现params参数，需要携带
        this.$router.push({ name: "search", query: this.$route.query });
      this.$bus.$emit("clear");
    },
    // 删除品牌信息
    removeTrademark() {
      this.searchParamas.trademark = undefined;
      //带给服务器参数都是可有可无的，如果属性值为空，还是会把相应的字段带给服务器，但是如果把相应的字段变为undifined，则不会带给服务器
      this.searchParamas.category1Id = undefined;
      this.searchParamas.category2Id = undefined;
      this.searchParamas.category3Id = undefined;
      this.getData();
      if (this.$route.query || this.$route.params)
        //删除后，返回到search路由,但如果路径当中出现params参数，需要携带
        this.$router.push({
          name: "search",
          query: this.$route.query,
          params: this.$route.params,
        });
    },
    removeAttrInfo(index) {
      this.searchParamas.props.splice(index, 1);
      this.getData();
    },
    //自定义事件的回调
    trademarkInfo(tradeMark) {
      // 1.整理品牌字段参数
      this.searchParamas.trademark = `${tradeMark.tmId}:${tradeMark.tmName}`;
      this.getData();
    },
    attrInfo(attr, attrValue) {
      let props = `${attr.attrId}:${attr.attrName}:${attrValue}`;
      if (this.searchParamas.props.indexOf(props) === -1)
        this.searchParamas.props.push(props);
      // this.getData()
    },
    changeOrder(flag) {
      let originOrder = this.searchParamas.order;
      let originFlag = originOrder.split(":")[0];
      let originSort = originOrder.split(":")[1];
      let newOrder = "";
      if (originFlag == flag) {
        //表明点击的是综合
        newOrder = `${flag}:${originSort == "desc" ? "asc" : "desc"}`;
      } else {
        //表明点击的是价格
        newOrder = `${flag}:'desc'`;
      }
      this.searchParamas.order = newOrder;
      this.getData();
    },
    //获取当前是第几页 自定义事件
    getPageNo(pageNo) {
      this.searchParamas.pageNo = pageNo;
      this.getData(); //别忘记继续向服务器发请求
    },
  },
  computed: {
    // mapGetters里面的写法传递的是数组，因为getters没有划分模块
    ...mapGetters(["goodsList"]),
    isOne() {
      return this.searchParamas.order.indexOf("1") != -1;
    },
    isTwo() {
      return this.searchParamas.order.indexOf("2") != -1;
    },
    isAsc() {
      return this.searchParamas.order.indexOf("asc") !== -1;
    },
    isDesc() {
      return this.searchParamas.order.indexOf("desc") !== -1;
    },
    // 获取search模块展示的数据（total）
    ...mapState({ total: (state) => state.search.searchList.total }),
  },
  //监听组件实例上属性的值的变化
  watch: {
    //监听路由的信息是否发生改变,如果发生变化，再次发起请求
    $route(newValue, oldValue) {
      Object.assign(this.searchParamas, this.$route.query, this.$route.params);
      this.getData();
      this.searchParamas.category1Id = "";
      this.searchParamas.category2Id = "";
      this.searchParamas.category3Id = "";
    },
  },
};
</script>

