<template>
  <div>
    <header class="header">
      <!-- 头部的第一行 -->
      <div class="top">
        <div class="container">
          <div class="loginList">
            <p>申屠欢迎您！</p>
            <!-- 没有登录 -->
            <p v-if='!userName'>    
              <span>请</span>
              <router-link to="/Login">登录</router-link>
              <router-link to="/Register" class="register"
                >免费注册</router-link
              >
            </p>
            <!-- 成功登录 -->
            <p v-else>
              <a >{{userName}}</a>
              <a class="register">退出登录</a>
            </p>
          </div>
          <div class="typeList">
            <a href="###">我的订单</a>
            <a href="###">我的购物车</a>
            <a href="###">我的</a>
            <a href="###">会员</a>
            <a href="###">企业采购</a>
            <a href="###">关注图图商城</a>
            <a href="###">合作招商</a>
            <a href="###">商家后台</a>
          </div>
        </div>
      </div>
      <!--头部第二行 搜索区域-->
      <div class="bottom">
        <h1 class="logoArea">
          <router-link class="logo" to="/home">
            <img src="./images/logo.png" alt="" />
          </router-link>
        </h1>
        <div class="searchArea">
          <form action="###" class="searchForm">
            <!-- 搜索框部分 -->
            <input
              type="text"
              id="autocomplete"
              class="input-error input-xxlarge"
              v-model="keyword"
            />
            <button
              class="sui-btn btn-xlarge btn-danger"
              type="button"
              @click="goSearch"
            >
              搜索
            </button>
          </form>
        </div>
      </div>
    </header>
  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      keyword: "",
    };
  },
  computed:{
    //用户名信息
    userName(){
      return this.$store.state.users.userInfo.name
    }
  },
  methods: {
    goSearch() {
      // 1.字符串形式传递参数
      // this.$router.push('/search/'+this.keyword+"?k="+this.keyword.toUpperCase())
      // 2.模板字符串
      // this.$router.push(`/search/${this.keyword}?k=${this.keyword}`)
      // 3.常用 对象写法
        // this.$router.push({
        //   name: "search",
        //   params: { keyword: this.keyword },
        //   // query: { keyword: this.keyword },
        // });
      // 如果params传递的是一个空串，则需要在空串后加或运算符然后加上undifined属性，否则路径会出现问题
      if (this.$route.query) {
        let location = {name: "search",params: { keyword: this.keyword || undefined }}
        location.query = this.$route.query
        this.$router.push(location)
      }
    },
  },
  mounted() {
    // 通过全局事件总线清除关键字
    this.$bus.$on('clear',()=>{
      this.keyword=''
    })
  },
};
</script>

<style lang='less' scoped>
.header {
  & > .top {
    background-color: #eaeaea;
    height: 30px;
    line-height: 30px;

    .container {
      width: 1200px;
      margin: 0 auto;
      overflow: hidden;

      .loginList {
        float: left;

        p {
          float: left;
          margin-right: 10px;

          .register {
            border-left: 1px solid #b3aeae;
            padding: 0 5px;
            margin-left: 5px;
          }
        }
      }

      .typeList {
        float: right;

        a {
          padding: 0 10px;

          & + a {
            border-left: 1px solid #b3aeae;
          }
        }
      }
    }
  }

  & > .bottom {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;

    .logoArea {
      float: left;

      .logo {
        img {
          width: 175px;
          margin: 25px 45px;
        }
      }
    }

    .searchArea {
      float: right;
      margin-top: 35px;

      .searchForm {
        overflow: hidden;

        input {
          box-sizing: border-box;
          width: 490px;
          height: 32px;
          padding: 0px 4px;
          border: 2px solid #4484CE;
          float: left;
          border-top-left-radius:3px ;
          border-bottom-left-radius: 3px;
        
          &:focus {
            outline: none;
          }
        }

        button {
          height: 32px;
          width: 68px;
          background-color: #4484CE;
          border: none;
          color: #fff;
           border-top-right-radius:3px ;
          border-bottom-right-radius: 3px;
          float: left;
          cursor: pointer;

          &:focus {
            outline: none;
          }
        }
      }
    }
  }
}
</style>