<template>
  <div class="spec-preview">
    <img :src="imgObj.imgUrl" />
    <div class="event" @mousemove="handler"></div>
    <div class="big">
      <img :src="imgObj.imgUrl" ref="big"/>
    </div>
    <!-- 遮罩层 -->
    <div class="mask" ref='mask'></div>
  </div>
</template>

<script>
  export default {
    props:['skuImageList'],
    name: "Zoom",
    data() {
      return {
        imgIndex:0
      }
    },
    methods:{
      handler(){
        let mask = this.$refs.mask
        let big = this.$refs.big
        let left = event.offsetX - mask.offsetWidth/2
        let top = event.offsetY - mask.offsetHeight/2
        if(left<0) left = 0
        if(left>=mask.offsetWidth) left = mask.offsetWidth
        if(top<0) top = 0
        if(top>=mask.offsetHeight) top = mask.offsetHeight
        //修改元素的left、top值
        mask.style.left = left + 'px'
        mask.style.top = top + 'px'
        big.style.left = -2*left + 'px'
        big.style.top = -2*top + 'px'
      }
    },
    mounted() {
      //使用全局事件总线获取兄弟传值
      this.$bus.$on('getImg',(index)=>{
        // 使用响应式数据修改数组内的值
        this.imgIndex = index
      })
    },
    computed:{
      imgObj(){
        return this.skuImageList[this.imgIndex]||{}   //如果服务器数据还没有回，至少获得一个空对象
      }
    }
  }
</script>

<style lang="less">
  .spec-preview {
    position: relative;
    width: 400px;
    height: 400px;
    border: 1px solid #ccc;

    img {
      width: 100%;
      height: 100%;
    }

    .event {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 998;
    }

    .mask {
      width: 50%;
      height: 50%;
      background-color: rgba(0, 255, 0, 0.3);
      position: absolute;
      left: 0;
      top: 0;
      display: none;
    }

    .big {
      width: 100%;
      height: 100%;
      position: absolute;
      top: -1px;
      left: 100%;
      border: 1px solid #aaa;
      overflow: hidden;
      z-index: 998;
      display: none;
      background: white;

      img {
        width: 200%;
        max-width: 200%;
        height: 200%;
        position: absolute;
        left: 0;
        top: 0;
      }
    }

    .event:hover~.mask,
    .event:hover~.big {
      display: block;
    }
  }
</style>