//引入路由组件
import Home from '@/pages/Home'
import Login from '@/pages/Login'
import Search from '@/pages/Search'
import Register from '@/pages/Register'
import Detail from '@/pages/Detail'
import AddCartSuccess from '@/pages/AddCartSuccess'
//路由配置信息
export default [
    {
        name:'home',
        path:'/home',
        component:Home,
        meta:{show:true}
    },
    {
        path:'/login',
        name:'login',
        component:Login,
        meta:{show:false}
    },
    {
        name:'search',
        path:'/search/:keyword?',     //指定一个params参数可传不可传
        component:Search,
        meta:{show:true},
        props:true                      //路由组件传递props参数,布尔值写法
    },
    {
        path:'/register',
        component:Register,
        meta:{show:false}
    },
    {
        name:'detail',
        path:'/detail/:skuId?',
        component:Detail,
        meta:{show:false}
    },
    {
        name:'addCartSuccess',
        path:'/AddCartSuccess/:skuID/:skuNum',
        component:AddCartSuccess,
        meta:{show:true},
    },
    //重定向，在项目跑起来时，访问/，立马让他定向到首页
    {
        path:'*',
        redirect:"/home"
    }
]