<template>
  <div class="pagination">
    <button :disabled='pageNo==1' @click='sendPage(pageNo-1)'>上一页</button>
    <button v-if='startPageAndEndPage.start>1' @click='sendPage(1)' :class="{active:1 === pageNo}">1</button>
    <button v-if='startPageAndEndPage.start>2'>···</button>

    <button v-for='(page,index) in startPageAndEndPage.end' :key = index v-if='page>=startPageAndEndPage.start&&page<=startPageAndEndPage.end' @click='sendPage(page)' :class="{active:page === pageNo}">{{page}}</button>

    <button v-if='startPageAndEndPage.end<totalPage-1'>···</button>
    <button v-if='startPageAndEndPage.end<totalPage' @click="sendPage(totalPage)" :class="{active:totalPage === pageNo}">{{totalPage}}</button>
    <button :disabled='pageNo==totalPage' @click='sendPage(pageNo+1)'>下一页</button>
    <button style="margin-left: 30px">共 {{total}} 条</button>
  </div>
</template>

<script>
export default {
  name: "Pagination",
  props: ["pageNo", "pageSize", "total", "continues"],
  computed: {
    totalPage() {
      return Math.ceil(this.total / this.pageSize);
    },
    startPageAndEndPage() {
      const { pageNo, totalPage, continues } = this;
      let start = 0;
      let end = 0;      
      start = pageNo - parseInt(continues/2)
      end = pageNo + parseInt(continues/2)
      if (start < 1) {
        start = 1;
        end = continues;
      }
      else if(end>totalPage){
        end = totalPage
        start = totalPage - continues + 1
      }
      return { start, end };
    },
  },
  methods: {
    sendPage(pageNo){
        this.$emit('getPageNo',pageNo)
    }
  },
};
</script>

<style lang="less" scoped>
.pagination {
  text-align: center;
  button {
    margin: 0 5px;
    background-color: #f4f4f5;
    color: #606266;
    outline: none;
    border-radius: 2px;
    padding: 0 4px;
    vertical-align: top;
    display: inline-block;
    font-size: 13px;
    min-width: 35.5px;
    height: 28px;
    line-height: 28px;
    cursor: pointer;
    box-sizing: border-box;
    text-align: center;
    border: 0;

    &[disabled] {
      color: #c0c4cc;
      cursor: not-allowed;
    }

    &.active {
      cursor: not-allowed;
      background-color: #409eff;
      color: #fff;
    }
  }
}
</style>