//登录与注册合并为一个模块
import {reqGetCode,reqRegister,reqUserLogin,reqUserInfo} from '@/api'
export default {
    state:{
        code:'',
        token:'',
        userInfo:{}
    },
    mutations:{
        GETSENDCODE(state,code){
            state.code = code
        },
        USERLOGIN(state,token){
            state.token = token
        },
        GETUSERINFO(state,userInfo){
            state.userInfo = userInfo
        }
    },
    actions:{
        async getSendCode({commit},phone){
            //获取验证码的接口把验证码返回了，但正常情况应该是后台把验证码发给客户 
            let result = await reqGetCode(phone)
            if(result.code==200){
                commit('GETSENDCODE',result.data)
                return 'ok'
            } else{     //需要告知用户验证码有没有发送成功
                return Promise.reject(new Error('faile'))
            }
        },
        //用户注册
        async UserRegister({commit},user){
            let result = await reqRegister(user)
            if(result.code==200){
                return 'ok'
            }
            else{
                return Promise.reject(new Error('faile'))
            }
        },
        //登录业务【token】令牌 是用户的唯一标识
        async userLogin({commit},data){
            let result = await reqUserLogin(data)
            if(result.code == 200){
                commit('USERLOGIN',result.data.token)
                return 'ok'
            }else{
                return Promise.reject(new Error(result.message))
            }
        },
        async getUserInfo({commit}){
            let result = await reqUserInfo()
            if(result.code == 200){
                commit('GETUSERINFO',result.data)
                return 'ok'
            }else{
                // return Promise.reject(new Error(result.message))
            }
        }
    },
    getters:{

    }
}