<template>
  <div class="type-nav">
    <div class="container">
      <!-- 利用时间委派 -->
      <div @mouseleave="leaveIndex" @mouseenter="enterShow">
        <h2 class="all">全部商品分类</h2>
        <!-- 过渡动画 -->
        <transition name="sort">
          <div class="sort" v-show="show">
            <div class="all-sort-list2">
              <!-- 一级分类 -->
              <div
                class="item"
                v-for="(c1, index) in categoryList"
                :key="c1.categoryId"
                :class="{ cur: currentIndex == index }"
                @click="goSearch"
              >
                <h3 @mouseenter="changeIndex(index)" class="item-span">
                  <a
                    :data-categoryName="c1.categoryName"
                    :data-category1Id="c1.categoryId"
                    >{{ c1.categoryName }}</a
                  >
                </h3>
                <!-- 二级,三级分类 -->
                <div
                  class="item-list clearfix"
                  :style="{ display: currentIndex == index ? 'block' : 'none' }"
                >
                  <div
                    class="subitem"
                    v-for="c2 in c1.categoryChild"
                    :key="c2.categoryId"
                  >
                    <dl class="fore">
                      <dt>
                        <a
                          :data-categoryName="c2.categoryName"
                          :data-category2Id="c1.categoryId"
                          class="item-span item-color" >{{ c2.categoryName }}</a
                        >
                      </dt>
                      <dd>
                        <em
                          v-for="c3 in c2.categoryChild"
                          :key="c3.categoryId"
                        >
                          <a
                            :data-categoryName="c3.categoryName"
                            :data-category3Id="c3.categoryId"
                            class="item-span item-color" >{{ c3.categoryName }}</a
                          >
                        </em>
                      </dd>
                    </dl>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>
      </div>
      <nav class="nav">
        <a href="###">服装城</a>
        <a href="###">美妆馆</a>
        <a href="###">图图超市</a>
        <a href="###">全球购</a>
        <a href="###">闪购</a>
        <a href="###">团购</a>
        <a href="###">有趣</a>
        <a href="###">秒杀</a>
      </nav>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import throttle from "lodash/throttle"; //按需引入
import search from "@/store/search";
export default {
  name: "TypeNav",
  data() {
    return {
      //存储用户鼠标移动到哪一个一级分类
      currentIndex: -1,
      show: true,
    };
  },
  mounted() {
    //当组件挂载完毕，将show属性变为false
    //如果不是home路由组件，将typenav隐藏
    if (this.$route.name !== "home") this.show = false;
  },
  computed: {
    ...mapState({
      //右侧的是一个函数，当使用计算属性时，右侧函数会执行一次
      //注入一个参数state，值为大仓库中仓库的值
      categoryList: (state) => state.home.categoryList,
    }),
  },
  methods: {
    //鼠标进入 修改currentIndex属性
    // changeIndex(index) {
    //   this.currentIndex = index;
    //   //用户慢慢操作，每一个一级分类都会触发鼠标进入事件
    //   //非正常情况，用户操作很快，经过测试，只有部分h3触发鼠标进入事件
    //   //是因为用户行为过快，浏览器反应不过来
    //   // console.log('鼠标进入',this.currentIndex);
    // },
    changeIndex: throttle(function (index) {
      this.currentIndex = index;
    }, 50),
    leaveIndex() {
      this.currentIndex = -1;
      if (this.$route.name !== "home") this.show = false;
    },
    goSearch(event) {
      // this.$router.push('/search')
      let element = event.target;
      let { categoryname, category1id, category2id, category3id } = element.dataset;
      let location = { name: "search" };
      let query = { categoryName: categoryname };
      if (categoryname) {
        if (category1id) {
          query.category1id = category1id;
        } else if (category2id) {
          query.category2id = category2id;
        } else {
          query.category3id = category3id;
        }
      }
      //如果路由跳转时有paramas参数，需要整合
      if(this.$route.params){
        location.params = this.$route.params
        location.query = query
        this.$router.push(location)
      }
      
    },
    //当鼠标移入时，将商品分类列表展示
    enterShow() {
      this.show = true;
    },
  },
};
</script>

<style scoped lang='less'>
.type-nav {
  border-bottom: 2px solid #4484CE;

  .container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    position: relative;

    .all {
      width: 210px;
      height: 45px;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
      background-color: #67a7f1;
      line-height: 45px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
    }

    .nav {
      a {
        height: 45px;
        margin: 0 22px;
        line-height: 45px;
        font-size: 16px;
        color: #333;
      }
    }

    .sort {
      position: absolute;
      left: 0;
      top: 45px;
      width: 210px;
      height: 461px;
      position: absolute;
      background: #fafafa;
      z-index: 999;

      .all-sort-list2 {
        .item {
          h3 {
            line-height: 30px;
            font-size: 14px;
            font-weight: 400;
            overflow: hidden;
            padding: 0 20px;
            margin: 0;

            a {
              color: #333;
            }
          }
          .item-span{
            cursor: pointer;
          }
          .item-list {
            display: none;
            position: absolute;
            width: 734px;
            min-height: 460px;
            background: #f7f7f7;
            left: 210px;
            border: 1px solid #ddd;
            top: 0;
            z-index: 9999 !important;

            .subitem {
              float: left;
              width: 650px;
              padding: 0 4px 0 8px;

              dl {
                border-top: 1px solid #eee;
                padding: 6px 0;
                overflow: hidden;
                zoom: 1;

                &.fore {
                  border-top: 0;
                  .item-color:hover{
                    color: rgb(1, 183, 255);
                  }
                }

                dt {
                  float: left;
                  width: 54px;
                  line-height: 22px;
                  text-align: right;
                  padding: 3px 6px 0 0;
                  font-weight: 700;
                }

                dd {
                  float: left;
                  width: 415px;
                  padding: 3px 0 0;
                  overflow: hidden;

                  em {
                    float: left;
                    height: 14px;
                    line-height: 14px;
                    padding: 0 8px;
                    margin-top: 5px;
                    border-left: 1px solid #ccc;
                  }
                }
              }
            }
          }
        }
        .cur {
          background-color: skyblue;
        }
      }
    }
    .sort-enter-active {
      animation: an 0.2s linear;
    }
    .sort-leave-active {
      animation: an 0.2s linear reverse;
    }
    @keyframes an {
      from {
        opacity: 0;
        height: 0px;
      }
      to {
        opacity: 1;
        height: 461px;
      }
    }
  }
}
</style>